<template>
    <div class="main">
        <div class="school_check">
            <el-page-header @back="goBack" :content="contentTitle"></el-page-header>
        </div>
        <div class="form_body">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="设备选择">
                    <el-button @click="screenClick">设备选择</el-button>
                </el-form-item>
                <el-form-item label="素材类型" prop="show_type">
                    <el-select v-model="ruleForm.show_type" placeholder="请选择" @change="showChange">
                        <el-option v-for="item in showTypeList" :key="item.id" :label="item.name" :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item :label="showTitle">
                    <el-button @click="showClick">素材选择</el-button>
                </el-form-item>
                <el-form-item>
                    <div v-if="ruleForm.show_type==2&&previewData.length>0">
                        {{'目前播放时长：'+timestr}}
                    </div>
                    <div v-if="previewData.length>0">
                        <template v-for="(item,index) in previewData">
                            <span :key="index" v-if="ruleForm.show_type==1&&item!='undefined'" class="par">
                                <el-image style="width: 100px; height: 100px;margin-right:5px;" :src="baseUrl+item.file_path" :preview-src-list="[baseUrl+item.file_path]">
                                </el-image>
                                <!-- <el-button icon="el-icon-close" size='mini' class="son" circle @click="del(item,index)"></el-button> -->
                            </span>
                            <span v-else-if="ruleForm.show_type==2&&item!='undefined'" :key='index' class="par">
                                <video height="100px" :src="baseUrl+item.file_path" @click="videoClick(baseUrl+item.file_path)"></video>
                                <!-- <el-button icon="el-icon-close" size='mini' class="sonv" circle @click="del(item,index)"></el-button> -->
                            </span>
                        </template>
                    </div>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
                    <!-- <el-button @click="resetForm('ruleForm')">取 消</el-button> -->
                </el-form-item>
            </el-form>
        </div>
        <el-dialog title="选择设备" :visible.sync="screenVisible" width="30%" @close="screenVisible=false" :close-on-click-modal="false">
            <el-form>
                <el-form-item style="display: block;">
                    <el-checkbox v-model="isSelectAll" @change="selectAll">全选/全不选</el-checkbox>
                    <el-tree :data="nodeData" show-checkbox check-on-click-node node-key="device_id" ref="tree" :props="defaultProps">
                    </el-tree>
                </el-form-item>
                <el-form-item>
                    <el-button @click="screenSubmit">提交</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog :title="showTitle" :visible.sync="showTypeVisible" width="40%" @close="showTypeVisible=false" :close-on-click-modal="false">
            <el-input placeholder="请输入素材名称" v-model="keyword" clearable size="small" style="width:30%;margin-bottom:10px;" @change="resView"></el-input>
            <div class="home_table table">
                <el-table :data="showNodeData" :row-key="getRowKey" ref="multipleTable" border element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%" @cell-click='cellClick' @selection-change='select'>
                    <el-table-column type="selection" width="55" :reserve-selection="true"></el-table-column>
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" align="center" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true"></el-table-column>
                    </template>
                    <el-table-column prop="address" label="素材预览" align="center" fixed="right" min-width="100">
                        <template slot-scope="scope">
                            <el-image v-if="scope.row.file_type==1" style="width: 50px; height: 50px" :src="baseUrl+scope.row.file_path" :preview-src-list="[baseUrl+scope.row.file_path]">
                            </el-image>
                            <video v-else-if="scope.row.file_type==2" :src="baseUrl+scope.row.file_path" height="50px"></video>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <!-- <div class="pagination">
                <el-pagination background :current-page.sync="page" :page-sizes="[5, 10, 15, 30]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<5? true:false" />
            </div> -->
        </el-dialog>
    </div>
</template>
<script>
import {
    drillView, deckArrangementInfo, deckArrangementAdd, deckArrangementEditApi, hashApi
} from '@/api/index';
export default {
    data() {
        return {
            contentTitle: '',
            ruleForm: {
                screen_name: '',//播放名称
                frequency: '',//播放次数
                screen_type: '',//设备类型
                show_type: '',//素材类型
            },
            rules: {
                screen_type: [
                    { required: true, message: '请选择设备类型', trigger: 'change' }
                ],
                show_type: [
                    { required: true, message: '请选择素材类型', trigger: 'change' }
                ],
            },
            screenTypeList: [],//设备类型列表
            showTypeList: [],//素材类型列表
            playType: [],//播放类型列表
            showTitle: '选择图片',//素材选择title
            showTypeVisible: false,//素材选择弹窗
            screenVisible: false,
            nodeData: [],//设备树状列表
            showNodeData: [],//素材表格列表
            defaultProps: {
                children: 'children',
                label: 'device_name',
            },
            // tree是否全选，复选框默认状态（）
            isSelectAll: false,
            device_ids: [],
            screen: [],
            showType: [],
            tableHead: [
                { title: '素材名称', prop: 'title', minWidth: '100' },
                { title: '素材时长', prop: 'duration', minWidth: '100' },
            ],
            tableHeight: window.innerHeight - 180,
            total: 0,
            page: 1,
            pageSize: 5,
            tableHeadCopy: [],
            baseUrl: '',
            keyword: '',
            file_ids: [],//素材id
            hash: '',
            previewData: [],
            infoId: '',
            dis: [],
            timestr: '',
        }
    },
    mounted() {
        this.baseUrl = process.env.UPLOADER_URL;
        this.infoId = this.$route.params.id;
        if (this.$route.params.id) {
            this.getInfo(this.infoId)
        }
        this.getHash();
        this.resView()
        this.tableHeadCopy = JSON.parse(JSON.stringify(this.tableHead));
    },
    methods: {
        getRowKey(row) {
            return row.id;
        },
        videoClick(e) {
            window.open(e, '_blank')
        },
        cellClick(row, column, cell, event) {
            if (row.file_type == 2 && column.fixed == 'right') {
                window.open(this.baseUrl + row.file_path, '_blank')
            }
        },
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.page = 1;
            this.pageSize = newSize;
            this.resView();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.page = newPage;
            this.resView();
        },
        selectAll() {
            if (this.isSelectAll) {
                // 	设置目前勾选的节点，使用此方法必须设置 node-key 属性
                this.$refs.tree.setCheckedNodes(this.nodeData);
            } else {
                // 全部不选中
                this.$refs.tree.setCheckedNodes([])
            }
        },
        //设备选择
        screenClick() {
            this.resView();
            this.screenVisible = true;
            this.isSelectAll = false;
            this.$nextTick(() => {
                if (this.screen.length > 0) {
                    this.getNodeData(this.screen);
                }
            })
        },
        async getNodeData(e) {
            this.nodeData = [];
            let data = {
                device_type: '1',
                file_type: this.ruleForm.show_type ? this.ruleForm.show_type : '',
                title: this.keyword,
            }
            const res = await this.axios.post(drillView, data)
            if (res.data.code == 1) {
                this.nodeData = res.data.data.device_list;
                if (e) {
                    this.$nextTick(function () {
                        this.$refs.tree.setCheckedKeys([]);
                        e.forEach((i) => {
                            var node = this.$refs.tree.getNode(i);
                            if (node) {
                                this.$refs.tree.setChecked(node, true);
                            }
                        });
                    })
                } else {
                    // 获取所有id 赋值树节点
                    const arr = [];
                    this.nodeData.forEach(data => {
                        arr.push(data.device_id)
                        if (data.children) {
                            data.children.forEach(item => {
                                arr.push(item.device_id)
                            })
                        }
                    });
                    this.$nextTick(function () {
                        this.$refs.tree.setCheckedKeys(arr);
                    })
                }

            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },

        //素材选择
        showClick() {
            this.keyword = '';
            this.resView();
            if (this.ruleForm.show_type == 1) {
                this.tableHead = this.tableHead.filter((e => {
                    return e.title !== '素材时长'
                }))
            } else {
                this.tableHead = this.tableHeadCopy;
            }
            this.showTypeVisible = true;
            this.$nextTick(() => {
                this.previewData.forEach(e => {
                    if (typeof (this.showNodeData.find(item => item.id == e.id)) != 'undefined') {
                        this.$refs.multipleTable.toggleRowSelection(this.showNodeData.find(item => item.id == e.id), true);
                    }

                })
            })
        },
        //素材类型改变事件
        showChange(e) {
            this.file_ids = [];
            this.previewData = [];
            if (e == 1) {
                this.showTitle = '选择图片';
            } else if (e == 2) {
                this.showTitle = '选择视频';
            }
        },
        goBack() {
            this.$router.go(-1);
        },
        async resView() {
            let data = {
                device_type: '1',
                file_type: this.ruleForm.show_type ? this.ruleForm.show_type : '',
                // curr: this.page,
                // pageSize: this.pageSize,
                title: this.keyword,
            }
            const res = await this.axios.post(drillView, data);
            if (res.data.code == 1) {
                this.nodeData = res.data.data.device_list;
                this.showNodeData = Object.prototype.toString.call(res.data.data.file_list) === '[object Object]' ? res.data.data.file_list.data : [];
                // this.total = res.data.data.file_list.total;
                this.showTypeList = res.data.data.show_type_list;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        //重置对话框表单
        resetForm() {
            this.ruleForm = {
                screen_name: '',//播放名称
                frequency: '',//播放次数
                screen_type: '',//设备类型
                show_type: '',//素材类型
            };
            this.showType = [];
            this.screen = [];
            this.$nextTick(() => {
                this.$refs['ruleForm'].resetFields();
            })
        },
        ///提交
        screenSubmit() {
            this.screen = this.$refs.tree.getCheckedKeys().concat(this.$refs.tree.getHalfCheckedKeys());
            this.screenVisible = false;
        },
        //表单提交
        submitForm() {
            this.$refs['ruleForm'].validate(async valid => {
                if (valid) {
                    await this.getHash();
                    let numReg = /^[0-9]*$/
                    let numRe = new RegExp(numReg)
                    let arr = [];
                    this.screen.forEach(e => {
                        if (numRe.test(e)) {
                            arr.push(e);
                        }
                    })
                    let data = {
                        rehearsal_id: JSON.parse(localStorage.getItem("Rehearsal_Id")),
                        show_type: this.ruleForm.show_type,
                        file_ids: this.file_ids.join(','),
                        device_ids: arr.join(','),
                        hash: this.hash,
                    };
                    let api;
                    if (typeof this.infoId != 'undefined') {
                        api = deckArrangementEditApi;
                        data.id = this.infoId;
                    } else {
                        api = deckArrangementAdd;
                    }
                    const res = await this.axios.post(api, data);
                    if (res.data.code == 1) {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'success'
                        });
                        this.$router.push('/drillManAdd')
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error'
                        });
                    }
                }
            })
        },
        select(selection) {
            let arr = [];
            if (selection.length > 0) {
                if (selection[0].file_type != selection[selection.length - 1].file_type) {
                    selection.forEach(e => {
                        if (e.file_type != selection[0].file_type) {
                            arr.push(e);
                        }
                    })
                } else {
                    arr = selection
                }
            } else {
                arr = selection
            }
            this.previewData = arr;
            if (arr.length > 0) {
                this.file_ids = [];
                arr.forEach(e => {
                    if (typeof e != "undefined") {
                        this.file_ids.push(e.id)
                    }
                })
            } else {
                this.file_ids = [];
            }
            let time = 0;
            this.previewData.forEach((e, index) => {
                if (typeof e != "undefined") {
                    if (e.file_type == 2) {
                        time += e.file_length
                    }
                }
            })
            this.timestr = this.timeChange(time)
        },
        async getHash() {
            const datas = {
                page_hash: 'multimediAdd'
            };
            const res = await this.axios.post(hashApi, datas);
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        // del(item, index) {
        //     let num = this.file_ids.findIndex(i => i == item.id);
        //     this.file_ids.splice(num, 1)
        //     this.previewData.splice(index, 1);
        //     let time = 0;
        //     this.previewData.forEach((e, index) => {
        //         if (typeof e != "undefined") {
        //             if (e.file_type == 2) {
        //                 time += e.file_length
        //             }
        //         }
        //     })
        //     this.timestr = this.timeChange(time)
        // },
        async getInfo(id) {
            const res = await this.axios.post(deckArrangementInfo, { id: id });
            if (res.data.code == 1) {
                this.ruleForm = res.data.data;
                this.device_ids = res.data.data.list
                this.screen = res.data.data.list
                this.file_ids = res.data.data.file_ids.length > 0 ? res.data.data.file_ids.split(',') : [];
                if (res.data.data.show_type == 1) {
                    this.showTitle = '选择图片';
                } else if (res.data.data.show_type == 2) {
                    this.showTitle = '选择视频';
                }
                this.dis = res.data.data.file_data;
                this.previewData = res.data.data.file_data;
                let time = 0;
                this.previewData.forEach((item, index) => {
                    if (typeof item != "undefined") {
                        time += item.file_length
                    }
                })
                this.timestr = this.timeChange(time)
                this.resView()
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        timeChange(value) {
            let result = parseInt(value);
            let y = Math.floor(result / 86400) < 10 ? "0" + Math.floor(result / 86400) : Math.floor(result / 86400);

            let h = Math.floor((result / 3600) % 24) < 10 ? "0" + Math.floor((result / 3600) % 24) : Math.floor((result / 3600) % 24);

            let m = Math.floor((result / 60) % 60) < 10 ? "0" + Math.floor((result / 60) % 60) : Math.floor((result / 60) % 60);

            let s = Math.floor(result % 60) < 10 ? "0" + Math.floor(result % 60) : Math.floor(result % 60);

            let res = "";
            if (y !== "00") res += `${y}天`;

            if (h !== "00") res += `${h}时`;

            if (m !== "00") res += `${m}分`;

            res += `${s}秒`;

            return res;
        }
    }
}
</script>
<style scoped lang='scss'>
.school_check {
    height: 60px;
    padding: 0 24px;
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.form_body {
    width: 30%;
    margin-left: 30%;
    .el-form {
        padding: 20px;
        .el-select {
            width: 26%;
        }
    }
}
.par {
    position: relative;
    .son {
        position: absolute;
        top: 0px;
        right: 0px;
    }
    .sonv {
        right: -8%;
        position: absolute;
        top: 0px;
    }
}
/deep/.el-dialog__body {
    height: 500px;
}
.table {
    height: 500px;
    overflow: overlay;
}
</style>


